import { schemeTableau10 } from 'd3-scale-chromatic';
import { decomposeColor } from '@mui/system/colorManipulator';
import { ftToM, tuple } from '../utils';
import type {
  ColorFilterState,
  ICatchAllFilter,
  TrajectoryMetadata,
  TrajectoryPlot,
  RGBAColor,
} from './types';
import { v4 } from 'uuid';
import { FLAT_UI_COLORS, TRIP_OPACITY, MUTED_TRIP_OPACITY } from './palette';

export const BASE_PALETTE_STRING = schemeTableau10.slice();

export const BASE_PALETTE = BASE_PALETTE_STRING.map((colorString) => {
  const c = decomposeColor(colorString);
  return tuple(c.values[0], c.values[1], c.values[2]);
});

export function getRandomPaletteColor(
  opacity: number = TRIP_OPACITY,
): RGBAColor {
  const idx = Math.floor(Math.random() * BASE_PALETTE.length);
  const c = BASE_PALETTE[idx];
  return [...c, opacity];
}

export function createColorPalette({
  count,
  offset = 0,
  opacity = TRIP_OPACITY,
}: {
  count: number;
  offset?: number;
  opacity?: number;
}): Array<RGBAColor> {
  const palette: Array<RGBAColor> = [];

  for (let i = 0; i < count; i++) {
    const color: RGBAColor = [
      ...BASE_PALETTE[(i + offset) % BASE_PALETTE.length],
      opacity,
    ];
    palette.push(color);
  }

  return palette;
}

const CATCH_ALL_NORMAL: ColorFilterState & { type: 'catchAll' } = {
  id: v4(),
  type: 'catchAll',
  enabled: true,
  displayName: 'All flights',
  color: [...FLAT_UI_COLORS.amethyst, TRIP_OPACITY],
};

const CATCH_ALL_MUTED: ColorFilterState & { type: 'catchAll' } = {
  id: v4(),
  type: 'catchAll',
  enabled: true,
  displayName: 'Others',
  color: [0x66, 0x66, 0x66, MUTED_TRIP_OPACITY],
};

export function createCatchAllFilter(
  mode: 'normal' | 'muted',
): ICatchAllFilter {
  const id = v4();

  if (mode === 'normal') {
    return { ...CATCH_ALL_NORMAL, id };
  }

  return { ...CATCH_ALL_MUTED, id };
}

export function createFilterMatcher(
  filter: ColorFilterState,
): (
  item: TrajectoryMetadata,
) => ((plot: TrajectoryPlot) => boolean | RGBAColor) | boolean {
  switch (filter.type) {
    case 'catchAll': {
      return () => true;
    }
    case 'aircraftOperator': {
      const prefixes = new Set(filter.aircraftOperators);
      return (item) =>
        !!item.aircraftOperator && prefixes.has(item.aircraftOperator);
    }
    case 'callsign': {
      const prefix = filter.startsWith.toUpperCase().trim();

      return (item) => {
        if (!item.callsign.startsWith(prefix)) {
          return false;
        }

        if (
          filter.maxLength != null &&
          item.callsign.length > filter.maxLength
        ) {
          return false;
        }

        if (
          filter.minLength != null &&
          item.callsign.length < filter.minLength
        ) {
          return false;
        }

        return true;
      };
    }
    case 'altitudeGradient': {
      const {
        topColor,
        bottomColor,
        topFt,
        bottomFt,
        excludeAbove = false,
        excludeBelow = false,
      } = filter;

      const topM = ftToM(topFt);
      const bottomM = ftToM(bottomFt);

      const sameColors =
        topColor[0] === bottomColor[0] &&
        topColor[1] === bottomColor[1] &&
        topColor[2] === bottomColor[2] &&
        topColor[3] === bottomColor[3];

      return (item) => {
        return (plot) => {
          const altitude = plot.altitudeM;

          if (excludeAbove && altitude > topM) {
            return TRANSPARENT;
          }

          if (excludeBelow && altitude < bottomM) {
            return TRANSPARENT;
          }

          if (sameColors) {
            if (altitude <= topM && altitude >= bottomM) {
              return topColor;
            }

            return TRANSPARENT;
          }

          const ratio = invlerp(bottomM ?? 0, topM, altitude);

          const r = lerp(bottomColor[0], topColor[0], ratio);
          const g = lerp(bottomColor[1], topColor[1], ratio);
          const b = lerp(bottomColor[2], topColor[2], ratio);
          const a = lerp(bottomColor[3], topColor[3], ratio);

          return [r, g, b, a];
        };
      };
    }
    // case 'altitudeRange': {
    //   const [bottom, top] = filter.rangeM;

    //   return () => (plot) => {
    //     const altitude = plot.altitudeM;

    //     return (
    //       altitude <= (top ?? Infinity) && altitude >= (bottom ?? -Infinity)
    //     );
    //   };
    // }
    case 'airport': {
      const prefixes = filter.airports.map((p) => p.toUpperCase().trim());

      return (item) => {
        return prefixes.some((prefix) => {
          if (
            (filter.airportFilterMode === 'departures' ||
              filter.airportFilterMode === 'all') &&
            item.departure?.startsWith(prefix)
          ) {
            return true;
          }

          if (
            (filter.airportFilterMode === 'arrivals' ||
              filter.airportFilterMode === 'all') &&
            item.destination?.startsWith(prefix)
          ) {
            return true;
          }

          return false;
        });
      };
    }
    default: {
      return () => false;
    }
  }
}

// https://www.trysmudford.com/blog/linear-interpolation-functions/
const lerp = (x: number, y: number, a: number) => x * (1 - a) + y * a;
const invlerp = (x: number, y: number, a: number) => clamp((a - x) / (y - x));
const clamp = (a: number, min = 0, max = 1) => Math.min(max, Math.max(min, a));

const TRANSPARENT: RGBAColor = [0, 0, 0, 0];
